import React, {Component} from "react";

import {connect} from "react-redux";
import {getOfferTypesForSelect, getSelectedVariantObject} from "eshop/modules/simfree/selectors";
import {getSelectedOffer, getSelectedOfferId} from 'eshop/modules/configurator/selectors/offers';
import {
    getMarketContext,
    getSelectedOfferType,
    getSelectedProcessTypeValue
} from "eshop/modules/configurator/selectors/filters"
import {setOfferType} from "eshop/modules/simfree/actions/app"
import ProductOfferInstalmentPriceTooltipComponent
    from "eshop/modules/simfree/components/ProductOfferInstalmentPriceTooltipComponent"
import OnlineUtils from "eshop/utils/OnlineUtils";
import OfferType from "../../constants/OfferTypeEnum";

import {getChangedDevice, getPriceIsNet} from "eshop/modules/cart/selectors"
import ProcessType from "../../../cart/enum/ProcessType";


class ProductDetailsDevicePriceComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        console.log("ProductDetailsDevicePriceComponent");
        console.log(this.props);
        if (ProcessType.SALE_OF_GOODS === this.props.selectedProcess) {
            return <SimfreePrice {...this.props}/>;
        }
        var proposition = this.props.selectedOffer;
        if (!proposition || !proposition.deviceData) return (null)
        let instalmentPayments = proposition.deviceData.inOffer.price.base.devicePayments;
        let oneTimePayment = this.props.showNet ? proposition.deviceData.inOffer.price.base.oneTimePaymentNet : proposition.deviceData.inOffer.price.base.oneTimePaymentGross;


        let useCartPrices = this.props.deviceToChange && !!this.props.deviceData.variantList.find(variant=> this.props.deviceToChange.productCode == variant.productId);
        if (useCartPrices) {
            instalmentPayments = this.props.deviceToChange.monthlyPrices;
            oneTimePayment = OnlineUtils.formatPrice(this.props.showNet ? this.props.deviceToChange.checkoutPrice.net : this.props.deviceToChange.checkoutPrice.gross)

        }


        let instalmentLength = instalmentPayments[0].monthTo;
        let instalmentValue = OnlineUtils.getPriceFromPaymentsObject(instalmentPayments[0], this.props.showNet);

        return (

            <div key={this.props.selectedOfferId} className="l-col l-col-12  u-padding-l u-small-no-padding-bottom">
                <div className="l-row u-no-spacing">
                    <div className="l-col l-col-7  l-col-small-6 u-large-no-padding-left">
                        <p className="h4">Cena</p>
                        {(this.props.selectedOfferType !== "SIMFREE_INST") &&
                        <p className="u-small-hide">Cena abonamentu nie jest zawarta w cenie telefonu</p>}
                    </div>
                    <div className="l-col l-col-5  l-col-small-6 u-text-right u-large-no-padding-right">
                        <div className="u-font-bold">
                            <span className="h2">{OnlineUtils.formatPriceAbsolute(instalmentValue)}</span>
                            <span
                                className="h4">,{OnlineUtils.formatPriceDecimals(instalmentValue)} z≈Ç x {instalmentLength} {OnlineUtils.odmienRaty(instalmentLength)}</span>
                            {OnlineUtils.updateOgTag('product:original_price:device_installment_price', OnlineUtils.formatPrice(instalmentValue))}
                            {OnlineUtils.updateOgTag('product:device_installment_loyalyty', instalmentLength)}
                            <ProductOfferInstalmentPriceTooltipComponent
                                instalmentTooltipDescription={this.props.descriptions.instalmentTooltipDescription}
                                proposition={proposition}
                            />

                        </div>
                        {instalmentPayments.length > 1 && instalmentPayments.map(
                            (instalmentPeriod, index) => {
                                if (index == 0)
                                    return (null)

                                return (<DeviceInstalmentPeriod
                                    key={"from_" + instalmentPeriod.monthFrom} {...instalmentPeriod}/>)
                            }
                        )
                        }
                        <p>+ {OnlineUtils.formatPrice(oneTimePayment)} z≈Ç na start</p>
                        {OnlineUtils.updateOgTag('product:original_price:device_start_price', OnlineUtils.formatPrice(oneTimePayment))}
                    </div>
                </div>
            </div>
        )
    }
}

const DeviceInstalmentPeriod = (props) => {
    let from = props.monthFrom || 0;
    let to = props.monthTo || props.maxInstalment;
    let instalmentPeriodLength = to - from + 1.0;
    let test = "";

    return (
        <h5 className="u-no-margin u-padding-top-s">{OnlineUtils.formatPrice(props.price)} z≈Ç
            x {instalmentPeriodLength} {OnlineUtils.odmienRaty(instalmentPeriodLength)}</h5>
    )

}
const SimfreePrice = (props) => {
    if (!props.selectedVariantObject.devicePaymentsData) {
        return (null);
    }
    let instalmentValue = props.selectedVariantObject.devicePaymentsData.oneTimePayment.price;
    if (props.showNet)
        instalmentValue = props.selectedVariantObject.devicePaymentsData.oneTimePayment.originalNetPrice;
    return (
        <div>
            <div className="l-col l-col-12  u-padding-l u-small-no-padding-bottom">
                <div className="l-row u-no-spacing">
                    <div className="l-col l-col-7  l-col-small-6 u-large-no-padding-left">
                        <h2 className="h4">Cena</h2>
                    </div>
                    <div className="l-col l-col-5  l-col-small-6 u-text-right u-large-no-padding-right">
                        <p style={{textAlign: "right"}} className="u-font-bold">
                            <span className="h1">{OnlineUtils.formatPriceAbsolute(instalmentValue)}</span>
                            <span className="h3">,{OnlineUtils.formatPriceDecimals(instalmentValue)} z≈Ç</span>
                            {OnlineUtils.updateOgTag('product:original_price:device_full_price', OnlineUtils.formatPrice(instalmentValue))}
                        </p>
                        <div style={{textAlign: "right"}}>
                            {props.offerTypesForSelect && props.offerTypesForSelect.filter(o => o.value === OfferType.SIMFREE_INST).length > 0 && !props.marketIsB2B &&
                            <a className="u-cursor-pointer"
                               onClick={props.setOfferType.bind(this, OfferType.SIMFREE_INST)}> lub sprawd≈∫ ofertƒô
                                raty&nbsp;0%</a>
                            }
                        </div>
                    </div>
                </div>
            </div>
            <div
                className="l-col l-col-12  u-padding-l u-padding-top-l u-medium-padding-left-l u-medium-padding-right-l u-small-hide">
                <div className="o-separator"></div>
            </div>
        </div>
    )
}


const mapStateToProps = state => ({
    selectedOfferType: getSelectedOfferType(state),
    selectedOffer: getSelectedOffer(state),
    selectedVariantObject: getSelectedVariantObject(state),
    selectedOfferId: getSelectedOfferId(state),
    offerTypesForSelect: getOfferTypesForSelect(state),
    marketIsB2B: getMarketContext(state) == "B2B",
    showNet: getPriceIsNet(state),
    selectedProcess: getSelectedProcessTypeValue(state),
    deviceToChange: getChangedDevice(state)
})

const mapDispatchToProps = dispatch => ({
    setOfferType: (offerType) => dispatch(setOfferType(offerType))
})

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetailsDevicePriceComponent)
